@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:"Lato", sans-serif}h1,h2,h3{color:#383838}.App{transition:all 0.5s ease}.library-active{margin-left:10%}.song-container{min-height:60vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.song-container img{width:15rem;height:15rem;border-radius:50%;object-fit:cover;object-position:center}@keyframes infinite-rotate{0%{-webkit-transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg)}}.song-container h2{font-family:"Satisfy", cursive;font-size:2rem;padding:3rem 1rem 1rem 1rem}.song-container h3{font-size:1rem}.img-rotate{animation:infinite-rotate 60s linear infinite}@keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}.rotateSong{animation:rotate 20s linear forwards infinite}.player{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.time-control{width:50%;display:flex;align-items:center;justify-content:center}.time-control input{width:100%;padding:0}.time-control p{padding:1rem}.play-control{display:flex;justify-content:space-between;align-items:center;padding:1rem;width:25%;min-width:15rem}.play-control svg{cursor:pointer}.track{width:100%;height:1rem;position:relative;overflow:hidden;border-radius:1rem}.animate-track{background:#ccc;width:100%;height:100%;position:absolute;top:0;left:0;transform:translateX(0%);padding:1rem;pointer-events:none}input[type="range"]{-webkit-appearance:none;width:100%;background:transparent}input[type="range"]::-webkit-slider-thumb,input[type="range"]::-moz-range-thumb{-webkit-appearance:none;background:transparent;border-color:transparent;color:transparent}input[type="range"]:focus{outline:none}input[type="range"]::-ms-track{width:100%;background:transparent;border-color:transparent;color:transparent}@media screen and (max-width: 768px){.time-control{width:90%}.play-control{width:80%}}.library{position:fixed;top:0;left:0;width:20rem;height:100%;background-color:white;box-shadow:2px 2px 50px #cacaca;overflow-y:scroll;transform:translateX(-100%);opacity:0;transition:all 0.5s ease;z-index:10}.library h2{text-align:center;padding:2rem}.library-song{display:flex;align-items:center;padding:1rem 2rem;cursor:pointer;transition:all 0.75s ease-out}.library-song img{width:5rem;height:5rem;border-radius:10px;object-fit:cover;object-position:center}.library-song:hover{background-color:#dbebff}.song-description{padding-left:1rem}.song-description h3{font-size:1rem;font-weight:700}.song-description h4{font-size:0.8rem;font-weight:400}*{scrollbar-width:thin;scrollbar-color:rgba(150,150,150,0.5)}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(150,150,150,0.5);border-radius:20px;border:transparent}.selected{background-color:#bed5ff}.active-library{transform:translateX(0);opacity:1}@media screen and (max-width: 768px){.library{width:100%}.library h2{text-align:left}}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav button{background:transparent;padding:0.5em 0.7em;border:2px solid #464646;cursor:pointer;transition:all 0.3s ease}nav button:hover{background:#464646;color:white}@media screen and (max-width: 768px){nav button{z-index:15}}

